import React from "react"
import { StaticQuery, Link, graphql } from "gatsby"
export default () => (
  <StaticQuery
    query={graphql`
      query {
        allDirectory(
          filter: { relativePath: { regex: "//20..$/" } }
          sort: { fields: name }
        ) {
          edges {
            node {
              id
              relativePath
            }
          }
        }
      }
    `}
    let
    year
    render={(data) => (
      <div>
        <span>Yearly Indexes: </span>
        {data.allDirectory.edges.map(({ node }) => (
          <span key={node.id}>
            <Link
              to={node.relativePath.split("pages")[1] + "/"}
              style={{
                padding: `0 2px`,
                textDecoration: `none`,
                color: `inherit`,
              }}
            >
              <span>{node.relativePath.split("/")[1]}</span>
            </Link>
            &#160;
          </span>
        ))}
      </div>
    )}
  />
)
